.profile-field-loader {
  width: 100%;
  height: 40px;
  border-radius: 10px;
}

.field-label-loader {
  width: 100px;
  height: 30px;
}

.partner-logo {
  background-size: cover;
  background-position: center;
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

@media (max-width: 600px) {
  .partner-logo {
    width: 80px;
    height: 80px;
  }
}
