.indicate-active {
  list-style: none;
  border-bottom: 5px solid transparent;
  /* border-bottom: 5px solid #eee; */
  transition: all 0.2s ease;
}

.indicate-active:hover {
  color: rgb(224, 216, 216);
  transform: scale(1.03);
}

.active-marker {
  background: white;
  bottom: 0;
  display: none;
  height: 2px;
  position: absolute;
  left: 0;
  transition: all 0.2s cubic-bezier(0.25, 0.25, 0.25, 1.25);
  width: auto;
  z-index: -1;
}

.cl-effect-4 a {
  padding: 0 0 10px;
}

.cl-effect-4 a::after {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 1px;
  background: #fff;
  content: "";
  opacity: 0;
  transition: height 0.3s, opacity 0.3s, transform 0.3s;
  transform: translateY(-10px);
}

.cl-effect-4 a:hover::after,
.cl-effect-4 a:active::after,
.cl-effect-4 a:focus::after {
  height: 5px;
  opacity: 1;
  transform: translateY(0px);
}

