.navbar {
  position: fixed;
  z-index: 1000;
  /* height: 100%; */
  width: 95vw;
  /* float: left; */
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  /* background: #ee3532cf; */
  /* background: #6e0009; */
  border-radius: 10px;
  color: #fff;
  font-family: "Poppins", sans-serif;
  transition: all 0.3s ease-out;
}
.blurred-nav {
  background: #ee3532a3 !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
  border-radius: 10px;
}

.dummy-div {
  height: 60px;
}

@media (min-width: 600px) {
  .dummy-div {
    height: 100px;
  }
}

.navbar .nav-container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.navbar .nav-header {
  float: left;
  padding: 10px 0;
}
.navbar .logo {
  width: 100%;
  max-width: 140px;
}
.navbar .logo a {
  font-weight: 700;
  text-decoration: none;
  color: #fff;
  font-size: 19px;
}
.navbar .logo img {
  /* max-width: 100%; */
  width: 110px;
  margin-top: -10px;
  height: 80px;
  border-radius: 30px;
}

@media (max-width: 600px) {
  .navbar .logo img {
    width: 60px;
    height: 40px;
  }
}
@media (max-width: 765px) {
  .active-marker {
    background: transparent;
    height: 0;
  }
}
.navbar .nav-links {
  margin: 0;
  float: right;
}
.navbar .nav-links .nav-item {
  position: relative;
  list-style: none;
  float: left;
  letter-spacing: 2px;
}
.navbar .nav-links .nav-item .nav-link {
  padding: 10px 15px;
  text-transform: uppercase;
  display: block;
  text-decoration: none;
  color: #fff;
}
.navbar .menu-show,
.menu-hide {
  transition: 0.4s;
  font-size: 30px;
  cursor: pointer;
  display: none;
}

.show-on-mobile {
  position: absolute;
  right: 60px;
}

@media (min-width: 768px) {
  .show-on-mobile {
    display: none;
  }
}

.navbar .menu-show {
  margin: 0;
  float: right;
}
.navbar .nav-link:hover,
.menu-show:hover,
.menu-hide:hover {
  color: gray;
}
.navbar #menu-toggle {
  position: absolute;
  visibility: hidden;
  z-index: -1;
}
@media (min-width: 768px) {
  .navbar .has-dropdown ul {
    position: absolute;
    left: inherit;
    top: 100%;
    width: 270px;
    padding: 0;
    border: unset;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    /* transform: translateY(-100vw); */
    background-color: #fff;
    transform: translateY(30px);
    -ms-box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05),
      -2px 0 5px 1px rgba(0, 0, 0, 0.05);
    -o-box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05),
      -2px 0 5px 1px rgba(0, 0, 0, 0.05);
    box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05),
      -2px 0 5px 1px rgba(0, 0, 0, 0.05);
    transform-origin: top;
    transition: all 500ms ease;
  }

  .has-dropdown {
    transition: all 0.3s ease-out !important;
  }
  .drop {
    cursor: pointer;
  }
  .navbar .has-dropdown:hover ul {
    transition: all 0.3s ease-out;
    visibility: visible;
    opacity: 1;
    z-index: 100000;
    transform: translateY(0);
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
    display: block !important;
  }
  .navbar .has-dropdown ul li {
    width: 100%;
    position: relative;
    display: block;
    line-height: 24px;
    font-weight: 500;
    font-size: 15px;
    text-transform: capitalize;
    color: #222;
    border-bottom: 1px solid rgba(34, 34, 34, 0.1);
    text-align: left;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
  }
  .navbar .has-dropdown ul li a {
    position: relative;
    display: block;
    padding: 15px 30px !important;
    line-height: 24px;
    font-weight: 500;
    font-size: 13px;
    text-transform: capitalize;
    color: #ee3632 !important;
    /* color: #6e0009 !important; */
    border-bottom: 1px solid rgba(34, 34, 34, 0.1);
    text-align: left;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
  }
  .navbar .has-dropdown ul li a:hover {
    background: #fd6a76;
    /* background: #6e0009; */
    color: #fff !important;
  }
}

@media (max-width: 767px) {
  .navbar .menu-show,
  .menu-hide {
    display: block;
  }
  .navbar .nav-links {
    position: fixed;
    width: 100%;
    height: 150vh;
    top: -20px;
    left: -200%;
    padding: 50px 0;
    transition: 1s;
    background: #ee3632;
    opacity: 0;
    /* background: #6e0009; */
  }
  .navbar .nav-links .nav-item {
    width: 100%;
  }
  .navbar .nav-links .nav-item .nav-link {
    padding: 15px;
    text-align: center;
  }
  .navbar .menu-hide {
    position: absolute;
    top: 15px;
    right: 20px;
  }
  .nav {
    position: absolute;
  }
  .navbar #menu-toggle:checked ~ .nav .nav-links {
    opacity: 1;
    left: 0vw;
  }
  .navbar .has-dropdown ul {
    display: none;
    position: relative;
  }
  .navbar .has-dropdown ul li {
    position: relative;
    /* background: #6e0009 !important; */
    background: #ee3632 !important;
    border-bottom: 1px solid #0000001c;
  }
  .navbar .dropdown-menu {
    transition: all 0.3s ease-out;
    margin: auto;
    width: 100%;
    padding: 0;
    border: 1px solid #0000001c;
    flex-direction: column;
  }
  .navbar .dropdown-menu li:not(:last-child) {
    border-bottom: 1px solid #0000001c;
  }
  .navbar .nav-links .has-dropdown .dropdown-menu .nav-link {
    font-size: 13px;
  }
}

