.my-image {
  object-fit: cover;
  width: 100% !important;
  height: 100% !important;
  max-height: 260px;
}

.store-dropdown-selector {
  width: 80vw;
  height: 80vh;
  max-width: 800px;
  max-height: 400px;
  min-width: 300px;
  min-height: 400px;
  overflow: scroll;
}

.each-store-option {
  background-color: #ffe7d0;
  border-radius: 10px;
  padding: 10px 20px;
  margin-top: 10px;
  width: calc(100% - 10px);
  font-weight: bold;
  color: #323232;
}

.edit-options {
  margin: 0 10px !important;
}

._100 {
  width: 100%;
  height: 100%;
}
